<template>
    <v-layout class="mb-4" row wrap>
        <v-flex v-if="error" md12>
            <v-alert color="red darken-2" type="error">
                {{ error }}
            </v-alert>
        </v-flex>
        <v-flex md12>
            <v-form v-model="valid">
                <v-flex md12>
                    <v-text-field :rules="required" outlined hint="The Displaying Name For Your Project in Firebase Console" v-model="p_name" label="Firebase Project Name"/>
                </v-flex>
                <v-flex>
                    <v-text-field :rules="required" outlined hint="The Project UNIQUE ID" v-model="p_id" label="Google Cloud Project ID"/>
                </v-flex>
            </v-form>
        </v-flex>
        <v-flex md12>
            <v-btn color="#fb9538" @click="next">
                Continue
            </v-btn>
        </v-flex>
    </v-layout>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "step-two",
        data(){
            return {
                p_name:null,
                p_id:null,
                valid:null,
                error:null,
                required:[
                    v => !!v || 'This Field is required',
                ]
            }
        },
        methods: {
            next: function () {
                if (this.valid){
                    this.p_id.replace(/\s+/g, '-').toLowerCase();
                    axios.post('http://127.0.0.1:8009/firebase/create',{
                        p_name:this.p_name,
                        p_id:this.p_id
                    }).then(res => {
                        let data = res.data;
                        if (data.status === "failed"){
                            this.error = data.msg;
                        } else {
                            this.error = null;
                            this.$emit('next');
                        }
                    }).catch(err => {
                        alert("An Error Happened, Please Submit the Problem in https://leadsuitcase.ml");
                        console.log(err);
                    });
                } else {
                    this.error = "Please fill the inputs"
                }
            }
        }

    }
</script>

